<template>
	<Upload multiple type="drag" name='avatar' :action="imgUrl" :on-success="onSuccess">
		<div style="padding: 20px 0">
			<Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
			<p>点击或将文件拖拽到这里上传</p>
		</div>
	</Upload>
</template>
<script>
export default {
  data() {
    return {
      imgUrl: this.getTest() + "/upload/upload",
      jsonurl: "",
      ids: [],
      AllData: [],
      DataLength: [],
      ProductData: [],
      oo: 0,
      Productfliter: {
        total: 0,
        limit: 3000,
        page: 1,
        name: ""
      },
      fliter: {
        total: 0,
        limit: 3000,
        page: 1,
        type: "1",
        SerialNumber: []
      }
    };
  },
  methods: {
    onSuccess(res, file) {
      this.DeleteAll();
      this.$http.get(`/api/avatar-output.json`).then(res => {
        this.AllData = res.data;
        this.DataLength = res.data.length;
        this.GetImageData();
      });
    },
    DeleteAll() {
      //全部删除
      this.ids = [];
      this.$http
        .post("https://www.laowaicang.com/warehouse/list", this.fliter)
        .then(res => {
          let that = this;
          let DeleteData = res.data.rows;
          DeleteData.forEach(function(value) {
            this.ids.push(value._id);
          }, this);
          this.$http
            .post(`https://www.laowaicang.com/warehouse/deletes`, {
              ids: this.ids.toString()
            })
            .then(res => {});
        });
    },
    GetImageData(index) {
      //获取相应需要的合作产品数据
      this.$http
        .post(`https://www.laowaicang.com/products/list`, this.Productfliter)
        .then(res => {
          this.ProductData = res.data.rows;
          this.GetImage(0);
        });
    },
    GetImage(index) {
      //图片匹配
      let AData = this.AllData;
      let that = this;
      let flag = true;
      let number = index
      this.ProductData.forEach(function(value) {
        if (value.name == AData[number].ProductName) {
          flag = false;
          AData[number].imgurl = value.imgurl;
          AData[number].type = "1";
          that.$http
            .post("https://www.laowaicang.com/warehouse/data", AData[number])
            .then(res => {
              if (number < 1460) {
                let dnumber = number+1
                that.GetImage(dnumber);
              }
            });
        }
      });
      if (flag) {
        if (index < 1460) {
          let dindex = index+1
          this.GetImage(dindex);
        }
      } else {
      }
    }
  },
  created() {
  }
};
</script>


